.explore {
  &__products {
    @include page-grid;

    row-gap: 40px;
  }
  &__product {
    grid-column: 1/ -1;

    img {
      transition: all 1s ease;
    }

    @include on-tablet {
      grid-column: 2/ -2;
    }

    @include on-desktop {
      grid-column: span 4;
    }
  }
}

.product__title {
  color: #d12d35;
}
